import "./App.css";
import React from 'react';
import {useState} from 'react';
import Axios from "axios";


function App() {
  var uri = 'https://reqres.in/api/users?page=1';
  var check =true;
  const [users,setUsers] = useState([]);

  async function getUsers() {
    if (check){
      
    var element = document.querySelector('.loader');
    element.style.visibility = 'visible';
    var result = await Axios.get(uri);
    setTimeout(function () {
      element.style.visibility = 'hidden';
      setUsers(result.data.data); 
      
    }, 2000 * 0.5 );
  }
    else{
     window.alert("Users Already Loaded");
    }
     
  }

  const data=[{name:"Sahil"}];

  return (<div>
    <div className="nav_bar">
      <h1>User Detail Cards üë©‚Äçüë¶</h1>
      <button className="getBtn" onClick={getUsers}>Get Users</button >
    </div>
    <div className="loader"></div>
    <div className="allUsers">
      {
        users.map((curElem) =>{
          console.log(curElem.first_name);
          {check =false};
          return (
            <div className="userBox">
            <div><img src={curElem.avatar} alt="user" className="userImage" /></div>
            <div className="content_box">
              <div className="content">First Name: <b>{curElem.first_name}</b></div>
              <div className="content">Last Name: <b>{curElem.last_name}</b></div>
              <div className="content">Email: <b>{curElem.email}</b></div>
            </div>
          </div>
          );
         
        })
        
      }
      </div>
  </div>
  );

}

export default App;
